.advantages {
  margin: 0 auto;
  width: 100%;
}

.advantages__header {
  box-sizing: border-box;
  margin: 0 auto;
  padding: 32px 28px;

  @media (min-width: @tablet-width) {
    padding: 47px 124px 55px;
  }
}

.advantages__title {
  .title;
  text-align: center;
  margin: 0;

  &--color {
    color: @white;
  }

  @media (min-width: @tablet-width) {
    .title-bigger(bigger; 18px);
  }
}

.advantages__description {
  text-align: center;
  display: block;
  margin: 0;
  margin-top: 15px;

  &--decor {
    &::before {
      content: "";
      position: relative;
      display: inline-block;
      top: -4px;
      right: 5px;
      width: 8px;
      height: 1px;
      background-color: @black;
    }

    &::after {
      content: "";
      position: relative;
      display: inline-block;
      top: -4px;
      left: 5px;
      width: 8px;
      height: 1px;
      background-color: @black;
    }
  }

  &--decor-color {
    &::before {
      content: "";
      position: relative;
      display: inline-block;
      top: -4px;
      right: 5px;
      width: 8px;
      height: 1px;
      background-color: @white;
    }

    &::after {
      content: "";
      position: relative;
      display: inline-block;
      top: -4px;
      left: 5px;
      width: 8px;
      height: 1px;
      background-color: @white;
    }
  }

  @media (min-width: @desktop-width) {
    margin-top: 25px;
  }
}

.advantages__list {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  margin: 0 auto;
  padding: 0;
  list-style-type: none;

  @media (min-width: @tablet-width) {
    flex-direction: row;
  }

  @media (min-width: @desktop-width) {
    flex-direction: row;
  }
}

.advantages__item {
  margin: 0 auto;

  @media (min-width: @tablet-width) {
    display: flex;

    &--full {
      width: 100%;
    }
  }
}

.advantages__container {
  padding: 33px 37px 40px;
  box-sizing: border-box;

  @media (min-width: @tablet-width) {
    min-height: 256px;
    width: 256px;
    padding: 52px 33px 31px;
    box-sizing: border-box;
  }

  @media (min-width: @desktop-width) {
    width: 400px;
    padding: 52px 57px 51px;
  }

  &--blue {
    color: @white;
    background-color: @blue;
  }

  &--grey {
    background-color: @gray;
  }
}

.advantages__image {
  @media (min-width: @tablet-width) {
    box-sizing: border-box;
    display: flex;
    min-height: 256px;
  }
}

.advantages__list-service {
  margin: 0;
  padding: 0 18px;

  @media (min-width: @tablet-width) {
    display: flex;
    flex-wrap: wrap;
  }

  @media (min-width: @tablet-width) {
    margin: 0 auto;
  }
}
